body
{
  margin: 0;
  padding: 0;
  background-color: #2f3640;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container
{
  width: 350px;
  padding: 20px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0 ,0.1);
}
.input-container
{
  width: 100%;
  display: flex;
  align-items: center;
  border: 2px solid #70e6f3;
  border-radius: 10px;
  overflow: hidden;
}
.cityInput
{
  flex:1;
  height: 30px;
  border: none;
  font-size: 16px;
  outline: none;
  padding: 10px;
}
.search-icon
{
  padding: 10px;
  height: 30px;
  cursor: pointer;
}
.icon
{
  margin-top: 3px;
  font-size: 25px;
}
.image
{
  text-align: center;
  height: 150px;
  margin-top: 25px;
}
.image img
{
  height: 160px;
  width: 200px;
  outline: none;
  border-radius: 10px;
  border: 1px solid #fff;
}
.temp,
.location,
.country
{
  margin-top: 5px;
  font-size: 35px;
  color: #333;
  text-transform: uppercase;
  text-align: center;
}
.temp{
  font-weight: 500;
}
.location
{
  color: #ffbc00;
  font-weight: 400;
  font-size: 40px;
}
.country{
  font-size: 18px;
  color: #888;
  font-weight: 500;
}
.cord
{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
  text-align: center;
}
.cord div{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
}
.cord div span{
  font-size: 14px;
}
.humidity-percent,
.wind-percent,
.cord div span :nth-child(2)
{
  font-size: 18px;
  font-weight: bold;
  color: #666;
  padding-top: 5px;
}
.element img
{
  height: 50px;
  width: 50px;
}
.data-container
{
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}
.element
{
  text-align: center;
}
.text
{
  font-size: 12px;
  color: #888;
}
.city-not-found,
.loading-message,
.error-message
{
  margin-top: 10px;
  color: #888;
  font-size: 25px;
  font-weight: 400;
  text-align: center;
}
